.header {
    position: relative;
    background-color: $pink-light-color;
  }
  .header .container {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
  
    padding-top: 6px;
    padding-left: 20px;
    z-index: 1;
    @media screen and (min-width: 480px) {
      left: calc((100% - 480px) / 2);
    }
    @media screen and (min-width: 768px) {
      left: calc((100% - 768px) / 2);
      padding-top: 37px;
      padding-left: 34px;
    }
    @media screen and (min-width: 1280px) {
      left: calc((100% - 1280px) / 2);
      padding-left: 46px;
    }
  }
  
  .nav {
    display: flex;
    align-items: baseline;
    @media screen and (max-width: 1279px) {
      display: none;
    }
  }
  
  .logo {
    position: absolute;
    top: 7px;
    left: 20px;
    @media screen and (min-width: 768px) {
      top: 37px;
      left: 34px;
    }
    @media screen and (min-width: 1280px) {
      left: 46px;
    }
  }
  
  .nav__list {
    display: flex;
    align-items: center;
    margin-right: 110px;
  }
  
  .nav__item {
    &:not(:last-child) {
      margin-right: 35px;
    }
  }
  
  .nav__link {
    font-weight: 700;
    font-size: 16px;
    line-height: 1.31;
    color: $white-color;
    transition: color $cubic;
    &:hover,
    &:focus {
      color: $titel-color;
    }
  }
  
  .button-burger {
    width: 20px;
    // height: 12px;
    height: 22px;
    padding: 0;
    color: $titel-color;
    border: none;
    background: transparent;
    margin-top: 19px;
    margin-right: 20px;
    cursor: pointer;
    @media screen and (min-width: 768px) {
      margin-top: -5px;
      margin-right: 30px;
    }
    @media screen and (min-width: 1280px) {
      display: none;
    }
    &:hover,
    &:focus {
      cursor: pointer;
    }
  }
  
  .button-submit {
    width: 160px;
    height: 45px;
    padding: 0;
    border-radius: 22px;
    border: none;
    cursor: pointer;
    box-shadow: 0px 4px 4px rgb(0 0 0 / 15%);
    background-color: $white-color;
  
    font-weight: 700;
    font-size: 16px;
    line-height: 1.31;
  
    color: $titel-color;
    transition: background-color $cubic;
    &:hover,
    &:focus {
      background-color: $hover-btn;
    }
    @media screen and (max-width: 767px) {
      display: none;
    }
  }
  
  // ------------------HERO----------------------- //
  .hero {
    height: 665px;
    position: relative;
    display: flex;
    overflow: hidden;
    background-color: $pink-light-color;
    height: 502px;
    @media screen and (min-width: 767px) {
      height: 400px;
    }
    @media screen and (min-width: 1279px) {
      height: 665px;
      justify-content: flex-end;
    }
    & .container {
      position: relative;
    }
  }
  
  .milk__box {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    bottom: 28px;
    @media screen and (max-width: 767px) {
      display: none;
    }
    @media screen and (min-width: 768px) {
      max-width: 768px;
    }
    @media screen and (min-width: 1280px) {
      left: 77px;
      bottom: 45px;
      max-width: 1280px;
    }
  }
  
  .milk__text {
    position: absolute;
    display: flex;
    flex-direction: column;
    left: 154px;
    bottom: 34px;
    @media screen and (min-width: 768px) {
      max-width: 768px;
    }
    @media screen and (min-width: 1280px) {
      left: 295px;
      bottom: 56px;
      max-width: 1280px;
    }
  }
  
  .hero__text {
    width: 114px;
    margin-bottom: 7px;
    font-weight: 400;
    font-size: 8px;
    line-height: 1.5;
    color: $white-color;
    @media screen and (max-width: 767px) {
      display: none;
    }
    @media screen and (min-width: 768px) {
      max-width: 768px;
    }
    @media screen and (min-width: 1280px) {
      width: 200px;
      font-size: 14px;
      line-height: 1.42;
      max-width: 1280px;
    }
  }
  
  .button-item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 26px;
    height: 26px;
    padding: 0;
    border-radius: 50%;
    border: none;
    cursor: pointer;
    box-shadow: 0px 4px 4px rgb(0 0 0 / 15%);
    fill: $titel-color;
    background-color: $white-color;
    transition: background-color $cubic;
    &:hover,
    &:focus {
      background-color: $hover-btn;
    }
    @media screen and (max-width: 767px) {
      display: none;
    }
  }
  
  .circle {
    position: absolute;
    height: 345px;
    width: 345px;
    border-radius: 50%;
    border: none;
    top: 45px;
    left: 36px;
    background-color: $section-label-color;
    @media screen and (min-width: 320px) {
      max-width: 480px;
      top: 83px;
      left: 36px;
      height: 346px;
      width: 344px;
    }
    @media screen and (min-width: 768px) {
      top: 31px;
      left: 310px;
      height: 324px;
      width: 322px;
      max-width: 768px;
    }
    @media screen and (min-width: 1280px) {
      top: 52px;
      left: 475px;
      height: 538px;
      width: 538px;
      max-width: 1280px;
    }
  }
  
  .hero__icecream {
    display: block;
    position: absolute;
    bottom: 0;
    right: 32px;
    z-index: 2;
    @media screen and (min-width: 320px) {
      max-width: 480px;
    }
    @media screen and (min-width: 768px) {
      left: 378px;
      max-width: 768px;
    }
    @media screen and (min-width: 1280px) {
      left: 594px;
      max-width: 1280px;
    }
  }
  
  .title-box {
    position: absolute;
    top: 193px;
    width: 218px;
    z-index: 3;
    @media screen and (min-width: 768px) {
      top: 102px;
      width: 162px;
    }
    @media screen and (min-width: 1280px) {
      top: 171px;
      width: 272px;
    }
  }
  
  .hero__title {
    margin-bottom: 19px;
    font-family: Titan One, sans-serif;
    font-weight: 400;
    font-size: 48px;
    line-height: 0.5;
    letter-spacing: 0.04em;
    text-transform: uppercase;
  
    color: $titel-color;
    @media screen and (min-width: 768px) {
      margin-bottom: 20px;
      font-size: 34px;
      line-height: 0.9;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 25px;
      font-size: 58px;
      line-height: 0.9;
    }
  }
  
  
  
  .button-box {
    display: flex;
  }
  
