  .hero__girl {
    display: block;
    position: absolute;
    bottom: 0;
    right: 34px;
    @media screen and (max-width: 767px) {
      display: none;
    }
    @media screen and (min-width: 1280px) {
      right: 110px;
    }
  }
  
  .hero__chain {
    position: absolute;
    top: 94px;
    right: 34px;
    @media screen and (max-width: 767px) {
      display: none;
    }
    @media screen and (min-width: 1280px) {
      top: 160px;
      right: 115px;
    }
  }
  
  .cafes {
    position: relative;
    z-index: 0;
    margin-bottom: 23px;
    @media screen and (min-width: 1280px) {
      margin-bottom: 15px;
    }
  }
  
  .food-trucks {
    position: relative;
    z-index: 0;
  }
  
  .chain {
    position: absolute;
    z-index: -1;
    top: 0;
    right: 0;
    width: 16px;
    height: 16px;
    border-radius: 50%;
    background: end;
    background-color: $white-color;
    @media screen and (min-width: 1280px) {
      width: 30px;
      height: 30px;
    }
  }
  
  .chain__title {
    font-family: Titan One, sans-serif;
    font-weight: 400;
    font-size: 28px;
    line-height: 1.14;
    letter-spacing: 4%;
  
    color: $titel-color;
    @media screen and (min-width: 1280px) {
      margin-bottom: 2px;
      font-size: 46px;
      line-height: 1.15;
    }
  }
  
  .chain__text {
    font-weight: 400;
    font-size: 8px;
    line-height: 1.25;
  
    color: $white-color;
    @media screen and (min-width: 1280px) {
      margin-bottom: 2px;
      font-size: 14px;
      line-height: 1.28;
    }
  }
  
  /* СТИЛИ ДЛЯ КНОПОК */
  
  .hero__list {
    display: flex;
  }
  
  .hero__item {
    &:not(:last-child) {
      margin-right: 8px;
  
      @media screen and (min-width: 1280px) {
        margin-right: 15px;
      }
    }
  }
  
  .hero__link {
    height: 24px;
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    border-radius: 22px;
    border: none;
  
    cursor: pointer;
  
    box-shadow: 0px 4px 4px rgb(0 0 0 / 15%);
    transition: background-color $cubic;
  
    font-weight: 400;
    font-size: 8px;
    line-height: 1.25;
  
    @media screen and (min-width: 1280px) {
      font-size: 14px;
      line-height: 1.28;
    }
  }
  
  .hero__link--products {
    width: 63px;
  
    color: $white-color;
    background-color: $titel-color;
  
    &:hover,
    &:focus {
      background-color: $hover-btnred;
    }
  
    @media screen and (min-width: 1280px) {
      width: 105px;
      height: 40px;
    }
  }
  
  .hero__link--him {
    width: 83px;
  
    color: $titel-color;
    background-color: $white-color;
  
    &:hover,
    &:focus {
      background-color: $hover-btn;
    }
  
    @media screen and (min-width: 1280px) {
      width: 140px;
      height: 40px;
    }
  }
  
  .mobile-menu__open-icon{
    fill: #D41443;
  }